//
// Custom components Config
// To override any custom variable safely you can use _variables.demo.scss
//

// Base icons(See: plugins/keenthemes-icons/font/ki.css)
$ki-types: (
        arrowDown: '\f109',
        arrowUp: '\f10e',
        arrowBack: '\f106',
        arrowNext: '\f105',
        boldClose: '\f113',
        close: '\f130',
        plus: '\f10d',
        minus: '\f10a',
        check: '\f108',
        hide: '\f115',
        code: '\f116',
        copy: '\f117'
) !default;

